import { Component, OnInit } from '@angular/core';
import {EjerciciosService} from '../../Others/Services/ejercicios/ejercicios.service'
import { AuthenticationService } from 'src/app/services/authentication.service';


@Component({
  selector: 'app-vegetacion',
  templateUrl: './vegetacion.page.html',
  styleUrls: ['./vegetacion.page.scss'],
})
export class VegetacionPage implements OnInit{

  ejercicios = []
  constructor(private ejerciosService: EjerciciosService,private authService: AuthenticationService) { }

  ngOnInit() {
    //Creo que se puede borrar en cualquier momento pero no lo he comprobado
    let tabs = document.querySelectorAll('.app-alumno');
    if (tabs !== null) {
        Object.keys(tabs).map((key) => {
            tabs[key].style.display = 'none';
        });
    }
   
    
    //Ejercicios a trav√©s del servicio.
    this.ejercicios =this.ejerciosService.getEjercicios('vegetacion');

    //Delete the option of answer and the lists of places once you enter to choose an exercise.
    localStorage.removeItem('allowAnswer');
    localStorage.removeItem('listReg');
    localStorage.removeItem('listLatinReg');
    localStorage.removeItem('listProv');
    localStorage.removeItem('listLatinProv');
    localStorage.removeItem('listRegTeacher');
    localStorage.removeItem('listLatinRegTeacher');
  }
  logout(){
    this.authService.logout();
  }
}
